import React from 'react';

import './render-riddle.css';

const RenderRiddle = ({itemData}) => {
  const { imgId} = itemData;

  return (
    <div  className={`riddle ${imgId}`}>
      <div className="render">
      {itemData.text}
      </div>
    </div>
  );
}

export default RenderRiddle;
