import React, { Component } from 'react';

import RenderRiddle from '../render-riddle';
import CodeInput from '../code-input';
import { textData, imgData, codeData} from './data'

import './app.css';

class App extends Component {
  textData = this.textData;
  imgData = this.imgData;
  codeData = this.codeData;

  state= {
    data: [
      this.createElement(0),
      this.createElement(1),
      this.createElement(2),
      this.createElement(3),
      this.createElement(4),
      this.createElement(5),
      this.createElement(6),
      this.createElement(7),
      this.createElement(8),
      this.createElement(9),
      this.createElement(10),
    ],
    currentId: 0
  }

  newRender = (code) => {
    const { data } = this.state;

    const newState = data.find((item) => item.code === code)
    if(newState) {
      this.setState({
        currentId: newState.id
      })
    }else {
      this.setState({
        currentId: 10
      })
    }
  }

  createElement(id) {
    return {
      id,
      code: codeData[id],
      text: textData[id],
      imgId: imgData[id]
    }
  }

  render() {
    const { currentId, data} = this.state;

    const currentItem = data.find((item) => item.id === currentId)

    return (
    <div className="App">
    <CodeInput onRenderRiddle={this.newRender}/>
    <RenderRiddle itemData={currentItem}/>
    </div>
    );
  }
}

export default App;
